import React, {useState, useEffect}from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


let divbox = styled.div`
    padding:20px;
`;

function Detail(props) {
    let history = useHistory();
    let {id} = useParams();
    let [showAlert, setShowAlert] = useState(true);
    let [tmpstate, setTmpState] = useState();
    console.log(tmpstate);
    useEffect( ()=>{
        let timer = setTimeout(()=>{
            setShowAlert(false);
        }, 2000);
        console.log("test");
        return ()=>{}
    },[showAlert]);
   
    
    let product = props.shoes.find(
        function(product){
            return product.id == id;
        }
    );

    return(
    <div className="container">
        <div className="red">제목</div>
        { showAlert ? (
        <div className="my-alert my-alert2">
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>) : null
        }
        <input onKeyDown={(e)=>{setTmpState(e.target.value)}}></input>
        <div className="row">
            <div className="col-md-6">
                <img src={"https://codingapple1.github.io/shop/shoes"+(parseInt(id)+1)+".jpg"} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
                <h4 className="pt-5">{product.title}</h4>
                <p>{props.shoes[id].content}</p>
                <p>{props.shoes[id].price}원</p>
                <Info stock={props.stock} ></Info>
                <button className="btn btn-danger" onClick={ ()=>{props.setStock([1,1,1]);} }>주문하기</button> 
                <button className="btn btn-danger" onClick={ ()=>{history.goBack();} }>뒤로가기</button> 
            </div>
        </div>
    </div>
    );
}

function Info(props){
    return(
        <div>재고 : {props.stock[2]}</div>
    );
}

export default Detail;